import axios from "axios";
export default {
  data: function data() {
    return {
      groups: [],
      show: false,
      hover: false
    };
  },
  created: function created() {
    var _this = this;

    axios.get(this.$store.state.apiURL + "/group/user/" + this.$store.state.user._id, {
      headers: {
        token: localStorage.getItem("token")
      }
    }).then(function (res) {
      _this.groups = res.data;
    });
  }
};